import React, { useState } from "react";
import { connect } from "dva";
import { menuList } from "./allNeedData/routesAndMenu";

import { Menu, Icon } from "antd";
import { Link } from "react-router-dom";

interface Greeting {     name: string }
interface State {
  user: string;
  
 [key: string]: string;
}

const { SubMenu } = Menu;
const Example = (props: Greeting) => {
  const onOpenChange = (e: any) => {
    console.log(e);
    console.log(props);
  };

  const [count, setCount] = useState(0);
  return (
    <div>
      <Menu mode="inline" onClick={e => onOpenChange(e)} style={{ width: 256 }}>
        {menuList.map((item: any) => {
          return item.children && item.children.length > 0 ? (
            <SubMenu
              key={item.name}
              title={
                <span>
                  <span>章节测试</span>
                </span>
              }
            >
              {item.children.map((itemss: any) => (
                <Menu.Item key={itemss.name}>
                  <Link to={itemss.path}>{itemss.name} </Link>
                </Menu.Item>
              ))}
            </SubMenu>
          ) : (
            <Menu.Item key={item.name}>
              <Link to={item.path}>{item.name} </Link>
            </Menu.Item>
          );
        })}
      </Menu>
    </div>
  );
};

export default connect(
  (state:State) => {
    return {
      state
    };
  },
  (dispatch: any) => {
    return {};
  }
)(Example);
